import { Button, Card, Form, InputNumber, message, Radio } from "antd";
import React from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../../components/UserManager";
import { post } from "../../utils/net";
import Back from "../../components/back/Back";
import "./MakeOrder.css";

function Order() {
  const key = "loading";

  const navigate = useNavigate();
  const auth = useAuth();
  const { username, password } = auth.userAuth;

  function onFinish(values: { chargeQuantity: number; chargeType: "F" | "T" }) {
    message.loading({ content: "...", duration: 0, key });
    post("userSendOrder", { username, password, ...values })
      .then(() => {
        message.success({ content: "", key });
        navigate(-1);
      })
      .catch(e => {
        message.error({ content: e.message, key });
      });
  }

  return (
    <>
      <Back title="" />
      <Card>
        <Form layout="vertical" onFinish={onFinish}>
          <Form.Item
            label=""
            name="chargeType"
            rules={[{ required: true, message: "" }]}
          >
            <Radio.Group>
              <Radio value="F"></Radio>
              <Radio value="T"></Radio>
            </Radio.Group>
          </Form.Item>

          <Form.Item
            label=""
            name="chargeQuantity"
            rules={[
              { required: true, message: "" },
              {
                type: "number",
                min: 1,
                message: "1",
              },
              {
                type: "number",
                max: 100,
                message: "100",
              },
            ]}
          >
            <InputNumber precision={0} addonAfter="kWH" />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </>
  );
}

export default Order;
